@import url(https://fonts.googleapis.com/css?family=Noto+Sans);
@import url(https://fonts.googleapis.com/css?family=Quicksand);
@import url(https://fonts.googleapis.com/css?family=Karla);
@import url(https://fonts.googleapis.com/css?family=Work+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.intro-title {
  font-size: 24px;
  font-weight: bold;
}




.navbar {
  background-color: black;
  color: white;
  overflow: auto;
  display: flex;
  align-items: center;
  margin: 0px;
}

.navbar h1 {
  color: white;
  float: left;
  flex: 1 1;
  margin-left: 20px;
  font-family: "Work Sans";
  font-size: 40px;
  margin: 8px 24px;
}

.navbar ul {
  list-style-type: none;
  overflow: hidden;
  background-color: black;
  margin: 0px;
  padding: 0px;
}

.navbar li {
  float: left;
}

.navbar li a {
  display: block;
  color: white;
  padding: 20px;
  font-family: "Quicksand";
  font-weight: 500;
  font-size: 20px;
  text-decoration: none;
}

.navbar li a:hover {
  background-color: slateblue;
}

@media screen and (max-width: 800px) {
  .navbar {
    flex-direction: column;
    overflow: hidden;
  }
  .navbar h1 {
    margin: 16px;
  }
}
#home {
  font-family: "Work Sans";
}

.home-section {
  padding: 48px;
}

#home-intro {
  color: white;
  background-color: darkslateblue;
}

#home-intro-text {
  max-width: 800px;
}

a {
  color: whitesmoke;
}
.project-card {
  margin: 16px;
}

h4 {
  color: darkslateblue;
  font-size: 24px;
  margin-bottom: 4px;
}

.link-button {
  color: darkslateblue;
}
h3 {
    font-size: 24px;
}
#blog {
    margin: 48px;
}

.blog-card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 16px;
    padding: 16px 24px;
    margin-top: 16px;
}
#advent-projects h2 {
    font-size: 32px;
}

#advent-projects a {
    color: black;
}
#timer {
    border-radius: 16px;
    padding: 24px;
    max-width: 500px;
    background-color: firebrick;
    color: white;
}

#time {
    margin: 8px 0 16px 0;
    font-size: 60px;
}

#timer-text {
    font-size: 24px;
    margin: 0px;
}

@media only screen and (max-width: 600px) {
    .round-marker-row {
        max-width: 500px;
        display: flex;
        flex-direction: row;
        margin-top: 8px;
        flex-wrap: wrap;
        align-items: flex-start;
        grid-gap: 4px;
        gap: 4px;
    }

    .round-marker-holder {
        height: 0;
        display: none;
    }

    .timer-button {
        margin-top: 12px;
    }
}

@media only screen and (min-width: 600px) {
    .round-marker-row {
        max-width: 500px;
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-evenly;
    }

    .round-marker-holder {
        width: 3px;
        height: 16px;
        background-color: black;
        position: relative;
        left: 50%;
        background-color: firebrick;
    }
    
    .round-marker-holder.long-holder {
        height: 36px;
    }
}

.round-marker {
    display: flex;
    flex-direction: column;
}

.round-marker-circle {
    border-radius: 50%;
    background-color: firebrick;
    width: 24px;
    line-height: 24px;
    padding: 8px;
    color: white;
    text-align: center;
}

.round-marker-circle.finished {
    border: 2px solid black;
}

.round-marker-circle.current {
    border: 4px solid black;
    width: 16px;
    line-height: 16px;
}

.timer-button {
    padding: 12px 16px;
    font-size: 16px;
    background-color: white;
    border: none;
    border-radius: 8px;
    color: firebrick;
    margin-right: 12px;
}

.uppercase {
    text-transform: uppercase;
}


